<template>
  <v-row>
    <v-col cols="12" class="text-center">
      <span class="display-2 font-weight-light">  </span>
    </v-col>
    <v-col cols="12">
      <v-row justify="center">
        <v-col cols="10">
          <v-timeline :dense="$vuetify.breakpoint.smAndDown">
            <v-timeline-item
              v-for="(t, i) in timeline"
              :key="i"
              :color="t.color"
              :icon="t.icon"
              fill-dot
            >
              <span slot="opposite" class="display-1">{{t.date}}</span>
              <v-card>
                <v-card-title class="title">{{t.title}}</v-card-title>
                <v-card-text class="text--primary">
                  <b v-if="!isMobile">{{t.date}}</b>
                  <p>{{t.description}}</p>
                </v-card-text>
              </v-card>
            </v-timeline-item>
          </v-timeline>
        </v-col>
      </v-row>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: 'timeline',
  data: () => ({
    timeline: [
      { color: 'green darken-3', icon: 'mdi-clock-start', title: '  ', description: '     (   +    +     ) +    ( )', date: '15 ' },
      { color: 'light-green darken-3', icon: 'mdi-camera-timer', title: '      ', description: '    ,           ', date: '10 ' },
      { color: 'lime darken-3', icon: 'mdi-camera-timer', title: '      ', description: '    ,          ', date: '18 ' },
      { color: 'yellow darken-3', icon: 'mdi-altimeter', title: '   ', description: '              ', date: '19 ' },
      { color: 'amber darken-3', icon: 'mdi-account-multiple-plus', title: '  ', description: '20  21            ,          ,            ', date: '20-21 ' },
      { color: 'orange darken-3', icon: 'mdi-account-multiple-plus', title: '  ', description: '22  23          ,      ,   80%    ', date: '22-23 ' },
      { color: 'deep-orange darken-3', icon: 'mdi-account-multiple-plus', title: '  ', description: '24  25            20%   ', date: '24-25 ' },
      { color: 'red darken-3', icon: 'mdi-star', title: '  ', description: '22, 24  26          /  ,  80%     20%   ', date: '26 ' }
    ]
  }),
  computed: {
    isMobile () {
      return ['xs', 'sm'].indexOf(this.$vuetify.breakpoint.name) === -1
    }
  }
}
</script>

<style lang="scss">
.v-card__title {
  word-break: normal !important;
}
</style>
